import debugExpression from "./debugExpression.js";

const calculate = (screen) => {
  const newExpression = debugExpression(screen);

  try {
    const result = eval(newExpression);
    screen.displayResult = true;
    screen.ans = result;
    return result;
  } catch {
    screen.displayError = true;
    return "Expresión Inválida";
  }
};

export default calculate;
